@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap");
@font-face {
  font-family: 'GreycliffCF';
  font-style: normal;
  font-weight: 700;
  src: url(./assets/fonts/GreycliffCF-Bold.woff2) format('woff2');
}
@font-face {
  font-family: 'GreycliffCF';
  font-style: normal;
  font-weight: 600;
  src: url(./assets/fonts/GreycliffCF-DemiBold.woff2) format('woff2');
}
@font-face {
  font-family: 'GreycliffCF';
  font-style: normal;
  font-weight: 500;
  src: url(./assets/fonts/GreycliffCF-Medium.woff2) format('woff2');
}
@font-face {
  font-family: 'GreycliffCF';
  font-style: normal;
  font-weight: 400;
  src: url(./assets/fonts/GreycliffCF-Regular.woff2) format('woff2');
}
@font-face {
  font-family: 'GreycliffCF';
  font-style: normal;
  font-weight: 300;
  src: url(./assets/fonts/GreycliffCF-Light.woff2) format('woff2');
}
*,
*:after,
*::before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  background-color: #f9f9f9;
  font-family: "Open Sans", sans-serif;
}
